import React from 'react';

import Icon from '../Icon';
import { StyledComponent } from './styles';
import useData from '../../hooks/useData';
import Link from '../Link';
import ListInlineItem from '../ListInlineItem';
import ListInline from '../ListInline';

const ListItem = ({ title, icon, link, color }) => (
  <ListInlineItem>
    <Link title={title} href={link} color={color} target="_blank">
      <Icon className="icon" iconName={icon} size="25" />
    </Link>
  </ListInlineItem>
);

const Skils = () => {
  const {
    data: { skils }
  } = useData();

  return (
    <StyledComponent>
      <h2>Habilidades</h2>
      <h3>Linguagens de programação e ferramentas</h3>
      <ListInline>
        {skils.map((skil, key) => (
          <ListItem key={key} {...skil} />
        ))}
      </ListInline>
    </StyledComponent>
  );
};

export default Skils;
