import React from 'react';
import useData from '../../hooks/useData';
import Icon from '../Icon';
import {
  HeroImage,
  HeroTitle,
  HeroSubTitle,
  HeroLocation,
  StyledComponent
} from './styles';
import Link from '../Link';
import ListInlineItem from '../ListInlineItem';
import ListInline from '../ListInline';

export interface HeroLinkProps {
  title: string;
  link: string;
  icon: string;
  color: string;
}

const HeroLink = ({ title, link, icon, color }: HeroLinkProps) => (
  <ListInlineItem>
    <Link href={link} title={title} color={color} target="_blanck">
      <Icon className="icon" iconName={icon} size="25" />
    </Link>
  </ListInlineItem>
);

const Header: React.FC = () => {
  const {
    data: {
      header: { thumb, title, subTitle, location },
      networkingLinks
    }
  } = useData();

  return (
    <StyledComponent>
      <HeroImage src={thumb} />
      <HeroTitle>{title}</HeroTitle>
      <HeroSubTitle>{subTitle}</HeroSubTitle>
      <HeroLocation>{location}</HeroLocation>
      <ListInline>
        {networkingLinks.map((link, key) => (
          <HeroLink key={key} {...link} />
        ))}
      </ListInline>
    </StyledComponent>
  );
};

export default Header;
