import React from 'react';
import useData from '../../hooks/useData';
import { DataEducationProps } from '../../contexts/DataContext';

import {
  StyledComponent,
  Title,
  StyledEducation,
  EducationInstitution,
  EducationCourse,
  EducationLocation,
  EducationPeriod
} from './styles';

const Education = ({
  course,
  institution,
  location,
  end,
  start
}: DataEducationProps) => {
  const startTime =
    start.month && start.year ? `${start.month} de ${start.year}` : start.year;
  const endTime =
    end.month && end.year ? `${end.month} de ${end.year}` : end.year;

  return (
    <StyledEducation>
      <EducationInstitution>{institution}</EducationInstitution>
      <EducationCourse>{course}</EducationCourse>
      <EducationLocation>{location}</EducationLocation>
      <EducationPeriod>{`${startTime} - ${endTime}`}</EducationPeriod>
    </StyledEducation>
  );
};

const Educations = () => {
  const {
    data: { educations }
  } = useData();

  return (
    <StyledComponent>
      <Title>Educação</Title>
      {educations.map((education, key) => (
        <Education key={key} {...education} />
      ))}
    </StyledComponent>
  );
};

export default Educations;
