import React from 'react';
import Loadable from 'react-loadable';
import * as iconsImported from '../Icons';

export type IconProps = {
  iconName: string;
  className?: string;
  flag?: Boolean;
  height?: Number | string;
  width?: Number | string;
  size?: Number | string;
  overlayOffset?: Number;
  overlayId?: string;
  overlayStopColor?: string;
};

const CAMEL_CASE_REGEX = /^[a-zA-Z]+([A-Z][a-z]+)+$/;

type LoadingProps = {
  height: Number;
  width: Number;
};

const Loading = (props: LoadingProps) => {
  const { height, width } = props;

  if (!height || !width) {
    return null;
  }

  const style = {
    height: `${height}px`,
    width: `${width}px`
  };

  return <span style={style}></span>;
};

// function to parse the iconName and avoid errors in the format names.
const parseIconName = (iconNameReceived: string) => {
  const icon = iconNameReceived.replace(/-/g, ' ');

  return icon
    .replace(/(?:^\w|[A-Z]|\b\w)/g, function(word) {
      return word.toUpperCase();
    })
    .replace(/\s+/g, '');
};

type CachedIconProps = { Component: any; props: any };
type CachedIconsProps = { [index: string]: CachedIconProps };
const cachedIcons = {} as CachedIconsProps;

const IconRender = (
  loaded: { default: any },
  props: { [x: string]: any; iconName: any }
) => {
  const Component = loaded.default;
  const { iconName, ...validProps } = props;

  cachedIcons[iconName] = { Component, props: validProps };

  return <Component {...validProps} />;
};

const Icon = (props: IconProps) => {
  const {
    iconName,
    className,
    flag,
    size,
    height,
    width,
    overlayOffset,
    overlayId,
    overlayStopColor
  } = props;

  type IconsType = {
    [name: string]: any;
  };
  const icons = iconsImported as IconsType;
  const icon = iconName || '';
  const flagName = icon.toUpperCase();
  let iconNameParsed = flag ? `CountryFlag${flagName}` : icon;

  if (!icon.match(CAMEL_CASE_REGEX)) {
    iconNameParsed = parseIconName(iconNameParsed);
  }

  const iconImport = icons[iconNameParsed];

  if (!iconImport) {
    return null;
  }

  interface sizeOptionsProps {
    height?: Number | string;
    width?: Number | string;
    size?: Number | string;
  }

  const sizeOptions = {} as sizeOptionsProps;

  if (size || (height && width)) {
    sizeOptions.height = size ? size : height;
    sizeOptions.width = size ? size : width;
    sizeOptions.size = size ? size : 0;
  }

  interface overlayOffsetOptions {
    'data-overlay-offset'?: Number;
    'data-overlay-id'?: string;
    'data-overlay-stop-color'?: string;
  }

  const overlayOffsetOptions = {} as overlayOffsetOptions;
  if (overlayOffset && overlayId) {
    overlayOffsetOptions['data-overlay-offset'] = overlayOffset;
    overlayOffsetOptions['data-overlay-id'] = overlayId;
    overlayOffsetOptions['data-overlay-stop-color'] = overlayStopColor;
  }

  const checkableName = iconImport.name || iconNameParsed;

  // Implement manual memory cache for loadable icons
  // based on icon name and props
  if (cachedIcons[checkableName]) {
    type currentIconPropsType = {
      className: any;
      height?: Number | string;
      width?: Number | string;
      [x: string]: any;
    };

    const currentIconProps = {
      className,
      ...sizeOptions
    } as currentIconPropsType;
    const { Component, props: cachedIconProps } = cachedIcons[checkableName];

    // Check if all props provided to the current Icon
    // are the same if compared to the cached version
    const hasSameProps = Object.keys(cachedIconProps).every((key: string) => {
      return (
        currentIconProps.hasOwnProperty(key) &&
        currentIconProps[key] === cachedIconProps[key]
      );
    });

    // If the props have changed, skip cached version
    if (hasSameProps) {
      return <Component {...cachedIconProps} />;
    }
  }

  // eslint-disable-next-line new-cap
  const LoadableIcon = Loadable({
    loader: iconImport,
    render: IconRender,
    loading: Loading.bind(null, { ...sizeOptions } as LoadingProps)
  });
  LoadableIcon.preload();

  return (
    <LoadableIcon
      {...sizeOptions}
      iconName={checkableName}
      className={className}
      {...overlayOffsetOptions}
    />
  );
};

export default React.memo(Icon);
