import { createContext } from 'react';

export interface DataHeaderProps {
  title: string;
  thumb: string;
  subTitle: string;
  location: string;
}

export interface DataNetworkingLinkProps {
  title: string;
  link: string;
  icon: string;
  color: string;
}

export interface DataSkilProps {
  title: string;
  icon: string;
  color: string;
}

export interface DataTimeProps {
  month: string | null;
  year: string | null;
}

export interface DataEducationProps {
  institution: string;
  location: string;
  course: string;
  tcc: string | null;
  start: DataTimeProps;
  end: DataTimeProps;
}

export interface DataPositionExperienceProps {
  title: string;
  location: string;
  description: string;
  start: DataTimeProps;
  end: DataTimeProps;
}

export interface DataExperienceProps {
  company: string;
  positions: Array<DataPositionExperienceProps>;
}

export interface DataFooterProps {
  author: string;
  year: Number | string;
}

export interface DataContextProps {
  header: DataHeaderProps;
  networkingLinks: Array<DataNetworkingLinkProps>;
  skils: Array<DataSkilProps>;
  educations: Array<DataEducationProps>;
  experiences: Array<DataExperienceProps>;
  footer: DataFooterProps;
}

const DataContext = createContext({} as DataContextProps);

export default DataContext;
