import React from 'react';

export interface ListInlineItemProps {
  className?: string;
  children?: React.ReactNode;
}

const ListInlineItem = ({
  children,
  className = '',
  ...props
}: ListInlineItemProps) => {
  const classes = `list-inline-item ${className}`;

  return (
    <li className={classes} style={{ marginBottom: '.5rem' }} {...props}>
      {children}
    </li>
  );
};

export default ListInlineItem;
