import React from 'react';
import useData from '../../hooks/useData';
import Icon from '../Icon';
import { StyledComponent } from './styles';

const Footer: React.FC = () => {
  const {
    data: {
      footer: { author, year }
    }
  } = useData();

  return (
    <StyledComponent>
      <p>
        <Icon iconName="FaRegCopyright" />
        {` ${author} ${year}`}
      </p>
    </StyledComponent>
  );
};

export default Footer;
