import React from 'react';
import useData from '../../hooks/useData';
import {
  DataExperienceProps,
  DataPositionExperienceProps
} from '../../contexts/DataContext';

import {
  StyledComponent,
  Title,
  Company,
  StyledPosition,
  PositionTitle,
  PositionPeriod,
  PositionLocation,
  PositionDescription
} from './styles';

const Position = ({
  end,
  start,
  description,
  location,
  title
}: DataPositionExperienceProps) => {
  const startTime =
    start.month && start.year ? `${start.month} de ${start.year}` : start.year;
  const endTime =
    end.month && end.year ? `${end.month} de ${end.year}` : end.year;

  return (
    <StyledPosition>
      <PositionTitle>{title}</PositionTitle>
      <PositionPeriod>{`${startTime} - ${endTime}`}</PositionPeriod>
      <PositionLocation>{location}</PositionLocation>
      <PositionDescription>{description}</PositionDescription>
    </StyledPosition>
  );
};

const Experience = ({ company, positions }: DataExperienceProps) => (
  <div>
    <Company>{company}</Company>
    {positions.map((position, key) => (
      <Position key={key} {...position} />
    ))}
  </div>
);

const Experiences = () => {
  const {
    data: { experiences }
  } = useData();

  return (
    <StyledComponent>
      <Title>Experiências</Title>
      {experiences.map((experience, key) => (
        <Experience key={key} {...experience} />
      ))}
    </StyledComponent>
  );
};

export default Experiences;
