import 'bootstrap/dist/css/bootstrap.min.css';

import { createGlobalStyle } from 'styled-components';

export default createGlobalStyle`
  * {
    box-sizing: border-box;
  }

  html,
  body {
    padding: 0;
    margin: 0;
  }

  .App {
    font-family: "Roboto", sans-serif;
    background-color: #ffffff;
    color: #404040;

    font-size: 14px;

    @media (min-width: 576px) { 
      font-size: 16px;
    }
  }

  h1, h2 {
    font-weight: bold;
  }

  h1 {
    font-size: 26px;

    @media (min-width: 576px) { 
      font-size: 30px;
    }

    @media (min-width: 768px) { 
      font-size: 34px;
    }
  }

  h2 {
    font-size: 22px;

    @media (min-width: 576px) { 
      font-size: 26px;
    }

    @media (min-width: 768px) { 
      font-size: 30px;
    }
  }

  h3 {
    font-size: 18px;

    @media (min-width: 576px) { 
      font-size: 22px;
    }

    @media (min-width: 768px) { 
      font-size: 26px;
    }
  }

  h4 {
    font-size: 16px;

    @media (min-width: 576px) { 
      font-size: 20px;
    }

    @media (min-width: 768px) { 
      font-size: 24px;
    }
  }
`;
