import React from 'react';
import GlobalStyle from './styles/global';
import { Container } from 'reactstrap';

import DataContextProvider from './providers/DataContextProvider';
import HomePage from './pages/HomePage';

function App() {
  return (
    <div className="App">
      <DataContextProvider>
        <GlobalStyle />
        <Container>
          <HomePage />
        </Container>
      </DataContextProvider>
    </div>
  );
}

export default App;
