import React from 'react';

export interface ListInlineProps {
  className?: string;
  children?: React.ReactNode;
}

const ListInline = ({
  children,
  className = '',
  ...props
}: ListInlineProps) => {
  const classes = `list-inline ${className}`;

  return (
    <ul className={classes} {...props}>
      {children}
    </ul>
  );
};

export default ListInline;
